import React from 'react';
import FriendSearch from './FriendSearch'
import Popup from './../Shared/Modal.jsx'


class FriendView extends React.Component {
    constructor() {
        super();
        this.state = {
            friends: [],
            image: '',
            modalShow: false
        }
    }
    componentDidMount() {
        let context = this;
        socket.emit("getFriends", sessionStorage.getItem('user'));
        socket.on("friendData", function(data){
            console.log(data);
            context.setState({
                friends: data
            })
        });
    }
    getFriend(i, e) {
        let context = this;
        e.preventDefault();
        context.setState({
            modalShow: true
        });
        console.log(i);
    }
    getImage(i, e) {
        e.preventDefault();
        console.log(this.state.friends[i].image);
        return this.state.friends[i].image;
    }
    modalClose() {
        this.setState({
            modalShow: false
        });
    }


    render () {
        return(
            <div className="FriendView">
                <div className="friendTable">
                    {this.state.friends.map((data, index) => {
                        console.log(data);
                        return (
                            <div>
                            <a href="" data-key={index} onClick={this.getFriend.bind(this, index)} className="friend">{data.friend}</a>
                                <Popup show={this.state.modalShow} onHide={this.modalClose.bind(this)} content={data.user} title={this.getImage.bind(this, index)} />
                            </div>
                        )
                    })}
                </div>
            </div>
        );
    }
}
export default FriendView



